@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&display=swap');

.header {
    background-color: rgba(12, 17, 27, 1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
    height: 84px;
}

.header__img {
    width: 67px;
    height: 63px;
    padding-left: 30px;
}

.nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.nav__item {
    margin-right: 30px;
    padding-left: 8px;
    padding-right: 8px;
    height: 50px;
    align-items: center;
}

.nav__item_link {
    position: relative;
    padding-right: 50px;
    top: 35%;
    left: 20%;
}

.nav__item:hover {
    border-radius: 15px;
    box-shadow: 5px 5px 15px rgba(255, 255, 255, 1);
    transition: 0.5s;
}

.nav__item_active {
    border-top: black solid 1px;
    border-bottom: black solid 1px;
}

img {
    display: block;
    max-width: 100%;
}

ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

a {
    color: rgba(255, 255, 255, 1);
    text-decoration: none;
}

.search {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 1s;
    width: 50px;
    height: 50px;
    background: white;
    box-sizing: border-box;
    border-radius: 25px;
    border: 4px solid white;
}

.inputsearch {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 42.5px;
    line-height: 30px;
    outline: 0;
    border: 0;
    display: none;
    font-size: 14px;
    border-radius: 20px;
    padding: 0 20px;
}

.fa {
    box-sizing: border-box;
    padding: 10px;
    width: 42.5px;
    height: 42.5px;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 50%;
    color: rgba(12, 17, 27, 1);
    text-align: center;
    font-size: 18px;
    transition: all 1s;
}

.search:hover {
    width: 300px;
    cursor: pointer;
}

.search:hover input {
    display: block;
}

.search:hover .fa {
    background: #07051a;
    color: white;
}

.modal {
    position: fixed;
    margin-top: 20px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: 0.5s all;
    z-index: 1000;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
    transition: 0.5s all;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}

.reg {
    position: absolute;
    left: 20%;
    top: 20%;
    width: 60%;
    margin: 0;
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(2, 3, 29);
    border-radius: 20px;
    transition: 0.5s;
    z-index: 2000;
}

.reg.active {
    background-color: rgba(12, 17, 27, 1);
}

.container {
    width: 600px;
    height: 250px;
    outline: solid;
    padding: 20px 0;
    border-radius: 15px;
    position: relative;
}

.block {
    height: 100%;
    background-color: rgba(255, 255, 255, 0.2);
    box-shadow: 0px 10px 30px rgba(255, 255, 255, 0.8);
    display: flex;
    align-items: center;
}

.block_item {
    width: 50%;
}

.block-item {
    text-align: center;
}

.block-item_title {
    font-size: 1.2rem;
    font-weight: 500;
    color: #fff;
    margin-bottom: 10px;
}

.block-item_btn {
    border: none;
    cursor: pointer;
    padding: 0px 20px;
    background-color: #fff;
    color: #333;
    font-size: 1.2rem;
    font-weight: 500;
}

.form-box {
    background-color: #fff;
    height: 100%;
    width: 50%;
    box-shadow: 0 5px 45px rgba(255, 255, 255, 0.8);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    transition: 0.5s ease-in-out;
    display: flex;
    overflow: hidden;
}

.form-box.active {
    left: 50%;
}

.form {
    width: 100%;
    padding: 50px;
    font-size: 16px;
    position: absolute;
    transition: 0.5s;
}

.form_signin {
    left: 0;
    transition-delay: 0.25s;
}

.form_signup {
    left: 100%;
    transition-delay: 0s;
}

.form-box.active .form_signin {
    left: -100%;
    transition-delay: 0s;
}

.form-box.active .form_signup {
    left: 0;
    transition-delay: 0.25s;
}

.form_title {
    font-size: 1.5rem;
    font-weight: 500;
    color: #333;
}

.form_input {
    width: 70%;
    padding: 10px;
    border: solid 1px #333;
}

.form_btn {
    border: none;
    cursor: pointer;
    font-size: 1.2rem;
    color: #fff;
    background-color: rgb(39, 43, 73);
    padding: 0px 20px;
}

button {
    border-radius: 10px;
}

.form_forgot {
    color: rgb(39, 43, 73);
    text-decoration: none;
}

p {
    padding-top: 10px;
}

.error {
    color: red;
    top: 0;
}


.wrapper {
    font-family: 'DM Sans', sans-serif;
    background-color: rgba(12, 17, 27, 1);
    background-size: cover;
    font-size: 18px;
    color: rgba(255, 255, 255, 1);
}


.footer-list-title {
    text-align: center;
    font-size: 18px;
    padding-bottom: 25px;
}

.footer-list-item {
    list-style: none;
    align-items: center;
    display: flex;
}


.footer-list-social {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-left: 30%;
    padding-right: 30%;
    gap: 48px;
}

.footer-link-fb {
    background-image: url(../img/facebook.svg);
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    background-position: center center;
}

.footer-link-instagram {
    background-image: url(../img/instagram.svg);
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    background-position: center center;
}

.footer-link-twitter {
    background-image: url(../img/twitter.svg);
    width: 24px;
    height: 19.5px;
    background-repeat: no-repeat;
    background-position: center center;
}

.footer-link-youtube {
    background-image: url(../img/youtube.svg);
    width: 22.76px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: center center;
}

.footer__nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-left: 400px;
    padding-right: 400px;
    gap: 48px;
}

.footer-data {
    text-align: center;
    font-size: 18px;
    padding-top: 24px;
    padding-bottom: 24px;
}

@media screen and (min-width: 768px) {
    .footer {
        max-width: 1440px;
        margin: 0 auto;
        padding-top: 58px;
    }

    .footer-list {
        max-width: 1440px;
        margin: 0 auto;
        padding-top: 58px;
    }
}

@media screen and (max-width: 767px) {
    .footer {
        margin: 0 20px;
        display: grid;
        grid-template-columns: 1fr;
        justify-content: center;
        align-items: flex-start;
        padding-top: 58px;
    }

    .contacts {
        grid-row: 5;
    }

    .social_media {
        grid-row: 7;
    }
}